import React from 'react';
import classnames from 'classnames';

export default class TabPanel extends React.Component {
  constructor(props) {
    super(props);

    this.state = {};
  }

  render() {
    const props = this.props;
    const prefixCls = `${props.prefixCls}-panel`;
    const cls = classnames({
      [prefixCls]: 1
    });
    return (
      <div
        className={cls}
      >
        {props.children}
      </div>
    );
  }
}
